.header {
    padding: 15px 20px;
    display: flex;
    justify-content: space-between;
    position: sticky;
    background-color: #212121;
    z-index: 100;
    top: 0;
    box-shadow: 0px 5px 8px -9px rgba(0, 0, 0, 0.75);
}

.app{
    background-color: #212121;
    color: white;
}

.header__left{
    display: flex;
    justify-content: space-evenly;
}

.header__left > img {
    height:40px
}

.header__input{
    display: flex;
    align-items: center;
    background-color: #e4d500;
    padding: 10px;
    margin-left: 10px;
    border-radius: 999px;
}

.header__input > input{
    border: none;
    background-color: transparent;
    outline-width: 0;
}

.header__middle{
    display: flex;
    flex: 1;
    justify-content: center;
}

.header__option > .MuiSvgIcon-root{
    color: #e4d500;
}

.header__option:hover > .MuiSvgIcon-root{
    color: #e4d500;
}

.header__option--active > .MuiSvgIcon-root{
    color: #e4d500;
}

.header__option--active{
    border-bottom: 4px solid #e4d500;
}

.header__option{
    display: flex;
    align-items: center;
    padding: 0 30px;
    cursor: pointer;
}

.header__option:hover{
    background-color: #eff2f5;
    border-radius: 10px;
    align-items: center;
    padding: 0 30px;
    border-bottom: none;
}

.header__info{
    display: flex;
    align-items: center;
    color: #e4d500;
}

.header__info > h4{
margin-left: 10px;
}

.header__right{
    display: flex;
}

.header__right:hover{
    color: #e4d500;
}

.icons:hover{
    color: #e4d500;
}